import PressCountTo from 'press-ui/press-count-to/press-count-to.vue';
import PressGridItem from 'press-ui/press-grid-item/press-grid-item.vue';
import PressGrid from 'press-ui/press-grid/press-grid.vue';


export default {
  i18n: {
    'zh-CN': {
      customStyle: '自定义样式',
      decimals: '小数位',
      separator: '千分位分隔符',
      duration: '自定义持续时间',
      animation: '禁用缓慢动画',
      control: '自定义控制',
      resume: '继续',
      pause: '暂停',
      start: '开始',
      countdown: '倒计时',
      slot: '插槽',
    },
    'en-US': {
      customStyle: 'Custom Style',
      decimals: 'Decimal',
      separator: 'Separator',
      duration: 'Duration',
      animation: 'Animation',
      control: 'Control',
      resume: 'Resume',
      pause: 'Pause',
      start: 'Start',
      countdown: 'Countdown',
      slot: 'Slot',
    },
  },
  components: {
    PressCountTo,
    PressGrid,
    PressGridItem,
  },
  data() {
    return {
      customStyle: 'margin-right: 16px;',
      autoplay: false,
      slotValue: '',
    };
  },
  methods: {
    start() {
      this.$refs.pressCountToRef.start();
    },
    pause() {
      this.$refs.pressCountToRef.stop();
    },
    resume() {
      this.$refs.pressCountToRef.resume();
    },
    onEnd() {
      this.onGTip('[end]');
    },
    onChange(value) {
      this.slotValue = value;
    },
  },
};
